import React, { useState, useEffect } from "react";

import {
  Container,
  Background,
  LogoImage,
  LogoRedirect,
  LoginContainer,
  Form,
  FormContent,
  ForgotPassword,
  Button,
  SecondaryButton,
  CreateAccountContainer,
  FormText,
} from "./styles";
import Logo from "../../assets/img/logob1.png";
import Input from "../../components/Input";
import api from "../../services/api";

function Login({ history }) {
  const [user, setUser] = useState({});
  const [isLogged, setIsLogged] = useState(false);

  function handleChange(e) {
    setUser({ ...user, [e.target.name]: e.target.value });
  }

  async function handleSubmit() {
    const resp = await api.post("/login", user);

    const { token } = resp.data;

    sessionStorage.setItem("token", token);

    if (token) {
      history.push("/player/shop");
    }
  }

  useEffect(() => {
    async function verifyToken(token) {
      const isLogged = await api.post("/checkLogin", {
        token,
      });
      if (isLogged) history.push("/player/shop");
    }
    const token = sessionStorage.getItem("token");
    if (token) verifyToken(token);
  });

  function enterPressed(e) {
    let code = e.keyCode || e.which;
    if (code === 13) {
      handleSubmit();
    }
  }

  return (
    <Container>
      <Background />
      <LoginContainer>
        <LogoRedirect href="/">
          <LogoImage src={Logo} alt="Logotipo do site" />
        </LogoRedirect>
        <Form>
          <FormContent>
            <Input
              title="E-mail"
              name="email"
              placeholder="example@example.com"
              onChange={handleChange}
              onKeyPress={enterPressed}
            />
            <Input
              title="Senha"
              name="password"
              type="password"
              placeholder="**************"
              onChange={handleChange}
              onKeyPress={enterPressed}
            />
            <ForgotPassword>Esqueceu sua senha?</ForgotPassword>

            <Button onClick={handleSubmit}>
              <span>Entrar</span>
            </Button>
            <CreateAccountContainer>
              <FormText>Não tem uma conta ainda?</FormText>
              <SecondaryButton href="/register">
                <span>Criar conta</span>
              </SecondaryButton>
            </CreateAccountContainer>
          </FormContent>
        </Form>
      </LoginContainer>
    </Container>
  );
}

export default Login;
