import React, { useState } from "react";

// reactstrap components
import {
  Table,
  Card,
  CardTitle,
  CardHeader,
  CardBody,
  Row,
  Col,
} from "reactstrap";

export const Donate = () => {
  const [donated, setDonated] = useState(true);
  return (
    <>
      <div className="content">
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <CardHeader>
                  <h5 className="title">Histórico de Doações</h5>
                </CardHeader>
              </CardHeader>
              <CardBody>
                {!donated ? (
                  <CardTitle tag="h5" className="text-center">
                    Você ainda não fez uma doação!
                  </CardTitle>
                ) : (
                  <Table className="tablesorter" responsive>
                    <thead className="text-primary">
                      <tr>
                        <th>Data da compra</th>
                        <th>Quantidade de moedas</th>
                        <th className="text-centre">Valor</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>05/01/2020</td>
                        <td>300</td>
                        <td className="text-centre">R$ 20</td>
                      </tr>
                      <tr>
                        <td>05/01/2020</td>
                        <td>300</td>
                        <td className="text-centre">R$ 20</td>
                      </tr>
                      <tr>
                        <td>05/01/2020</td>
                        <td>300</td>
                        <td className="text-centre">R$ 20</td>
                      </tr>
                    </tbody>
                  </Table>
                )}
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default Donate;
