import React from "react";

import {
  Button,
  Container,
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Row,
  Col,
} from "reactstrap";

import coinIcon from "../../assets/img/coin-icon.png";

export default function ShopCard({
  name,
  type,
  price,
  image,
  description,
  onClick,
}) {
  return (
    <Container>
      <Card className="shopcard--item">
        <CardHeader className="shopcard--item__header">
          <Row className="row">
            <Col md="4" className="col">
              <img className="shopcard--icon" src={image} alt={name}></img>
            </Col>
            <Col md="8" className="col">
              <h5 className="title">{name}</h5>
              <h5 className="subtitle">{type}</h5>
              <div className="shopcard--price">
                <span>{price}</span>
                <img src={coinIcon} alt="Coin" className="coin" />
              </div>
            </Col>
          </Row>
        </CardHeader>

        <CardBody>
          <p className="shopcard--description">{description}</p>
        </CardBody>
        <CardFooter className="shopcard--item__footer">
          <Button
            className="btn-fill .shopcard--button"
            color="info"
            onClick={onClick}
          >
            Comprar
          </Button>
        </CardFooter>
      </Card>
    </Container>
  );
}
