import React, { useState, useEffect, useRef } from "react";
import NotificationAlert from "react-notification-alert";

// reactstrap components
import {
  Button,
  Label,
  Card,
  CardHeader,
  CardBody,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  FormGroup,
  Form,
  Input,
  Row,
  Col,
} from "reactstrap";

import api from "../services/api";

const notificationSuccess = {
  place: "tc",
  message: <div>Seu pagamento foi realizado com sucesso!</div>,
  type: "success",
  autoDismiss: 4,
};
const notificationError = {
  place: "tc",
  message: <div>Ocorreu um erro durante o pagamento!</div>,
  type: "danger",
  autoDismiss: 4,
};
const notificationPrice = {
  place: "tc",
  message: <div>Selecione um valor para doação!</div>,
  type: "danger",
  autoDismiss: 1,
};

const Donate = () => {
  const [price, setPrice] = useState(0);
  const [modal, setModal] = useState(false);

  const paypalRef = useRef();
  const notificationSuccessRef = useRef();
  const notificationErrorRef = useRef();
  const notificationPriceRef = useRef();

  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://www.paypal.com/sdk/js?client-id=AWAVmrQWVaXPVK8g_fQs7MOooGPkg6ydQKkRGnU2MEH_mWQF7SvowuzNhjBl6ewoZ4gqMbvGy665Io6N&currency=BRL";
    document.body.appendChild(script);
    if (modal) {
      setTimeout(() => {
        window.paypal
          .Buttons({
            createOrder: (data, actions) => {
              return actions.order.create({
                purchase_units: [
                  {
                    amount: {
                      currency_code: "BRL",
                      value: price,
                    },
                  },
                ],
              });
            },
            onApprove: async (data, actions) => {
              const order = await actions.order.capture();
              console.log(order);
              api.post(
                "/donate",
                {
                  order_id: order.id,
                  amount: order.purchase_units[0].amount.value,
                  currency_code: order.purchase_units[0].amount.currency_code,
                  coins: order.purchase_units[0].amount.value,
                  status: order.status,
                  payer_name: order.payer.name.given_name,
                  payer_address: order.payer.address.country_code,
                  payer_email: order.payer.email_address,
                  created_at: order.create_time,
                },
                {
                  headers: {
                    authorization: sessionStorage.getItem("token"),
                  },
                }
              );
              notificationSuccessRef.current.notificationAlert(
                notificationSuccess
              );
              setModal(false);
            },
            onError: (err) => {
              console.error(err);
              notificationErrorRef.current.notificationAlert(notificationError);
            },
          })
          .render(paypalRef.current);
      }, 100);
    }
  }, [modal, price]);

  const handleSelect = (event) => {
    setPrice(event.target.value);
    console.log(price);
  };

  const handleModal = () => {
    if (price > 0) {
      setModal(true);
    } else {
      notificationPriceRef.current.notificationAlert(notificationPrice);
    }
  };

  return (
    <>
      <NotificationAlert ref={notificationSuccessRef} />
      <NotificationAlert ref={notificationErrorRef} />
      <NotificationAlert ref={notificationPriceRef} />
      <Modal isOpen={modal} backdrop={true}>
        <ModalHeader>Selecione o método de pagamento</ModalHeader>
        <ModalBody>
          <div ref={paypalRef}></div>
        </ModalBody>
        <ModalFooter>
          <Button color="warning" onClick={() => setModal(false)}>
            Cancelar
          </Button>
        </ModalFooter>
      </Modal>
      <div className="content">
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <h5 className="title">Faça uma doação</h5>
              </CardHeader>
              <CardBody>
                <Form>
                  <Row>
                    <Col md="4">
                      <FormGroup>
                        <Label for="donate--select">Quantidade</Label>
                        <Input
                          type="select"
                          name="select"
                          id="donate--select"
                          onChange={handleSelect}
                        >
                          <option value="0">Selecione o valor desejado</option>
                          <option value="10">R$ 10</option>
                          <option value="25">R$ 25</option>
                          <option value="50">R$ 50</option>
                          <option value="100">R$ 100</option>
                        </Input>
                      </FormGroup>
                    </Col>
                    <Col md="3"></Col>
                  </Row>
                  <Button
                    className="btn-fill .donate--button"
                    color="info"
                    onClick={handleModal}
                  >
                    Comprar Gold
                  </Button>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
};

export default Donate;
